<template>
    <!-- 会话页面 -->
    <div id="dialog">
        <van-nav-bar :fixed="true" :placeholder="true" left-arrow @click-left="backToHomeMessage()">
            <template #right>
                <van-icon name="wap-nav" size="20" />
            </template>
            <template #title>
                {{ name }}<br>
                <van-image
                    width="90"
                    height="10"
                    :src="require('@/assets/images/手机在线.png')" />
            </template>
        </van-nav-bar>
        
        <!-- 聊天会话内容区域 -->
        <div id="dialog_content" class="chat-container">
            <div class="messages" ref="messagesContainer">
                <!-- 初始欢迎消息 -->
                <div class="message-item ai-message">
                    <div class="avatar">
                        <van-icon name="robot" size="24" />
                    </div>
                    <div class="message-bubble">
                        你好！我是{{ name }}，有什么可以帮你的吗？
                    </div>
                </div>
                
                <!-- 动态消息列表 -->
                <div 
                    v-for="(msg, index) in messages" 
                    :key="index" 
                    :class="['message-item', msg.sender === 'user' ? 'user-message' : 'ai-message']"
                >
                    <div class="avatar">
                        <van-icon 
                            :name="msg.sender === 'user' ? 'user' : 'robot'" 
                            size="24" 
                        />
                    </div>
                    <div 
                        class="message-bubble" 
                        :class="msg.isError ? 'error-message' : ''"
                    >
                        {{ msg.content }}
                    </div>
                </div>
                
                <!-- 正在输入指示器 -->
                <div 
                    v-if="isTyping" 
                    :id="typingId"
                    class="message-item ai-message"
                >
                    <div class="avatar">
                        <van-icon name="robot" size="24" />
                    </div>
                    <div class="message-bubble typing">
                        <span></span>
                        <span></span>
                        <span></span>
                    </div>
                </div>
            </div>
        </div>
        
        <div id="dialog_bottombar">
            <div id="dialog_bottombar_inside">
                <van-field 
                    type="textarea" 
                    rows="1" 
                    :autosize="{maxHeight:100, minHeight:10}" 
                    v-model="message" 
                    style="border-radius:25px; padding:5px;" 
                    @keypress.enter.prevent="sendMessage"
                    placeholder="输入消息..."
                />
                <van-row>
                    <van-col span="4" style="text-align:center;">
                        <van-icon :name="require('@/assets/icon/icon_dialog_语音.png')" size="20" />
                    </van-col>
                    <van-col span="4" style="text-align:center;">
                        <van-icon :name="require('@/assets/icon/icon_dialog_图片.png')" size="20" />
                    </van-col>
                    <van-col span="4" style="text-align:center;">
                        <van-icon :name="require('@/assets/icon/icon_dialog_相机.png')" size="20" />
                    </van-col>
                    <van-col span="4" style="text-align:center;">
                        <van-icon :name="require('@/assets/icon/icon_dialog_红包.png')" size="20" />
                    </van-col>
                    <van-col span="4" style="text-align:center;">
                        <van-icon :name="require('@/assets/icon/icon_dialog_表情.png')" size="20" />
                    </van-col>
                    <van-col span="4" style="text-align:center;">
                        <van-button 
                            round 
                            type="primary" 
                            size="small"
                            @click="sendMessage"
                            style="width:35px; height:35px; padding:0; display:flex; align-items:center; justify-content:center;"
                        >
                            <van-icon name="paper-plane-o" size="16" />
                        </van-button>
                    </van-col>
                </van-row>
                <div id="dialog_bottombar_inside_funcrow">
                </div>
            </div>
        </div>
        
        <!-- 服务状态提示 -->
        <div class="status-bar" :class="statusClass">
            {{ statusText }}
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            name: this.$route.params.name, // 当前的聊天对象名字
            message: "", // 正在输入的消息
            messages: [], // 消息列表
            isTyping: false, // 是否正在输入状态
            typingId: "", // 正在输入指示器ID
            API_URL: 'https://tender-secure-bluegill.ngrok-free.app/api/chat', // ngrok代理的后端地址
            statusText: '连接中...', // 状态文本
            statusClass: 'text-xs text-gray-500 mt-2 text-center', // 状态样式
            // 通用请求头（包含ngrok跳过警告和内容类型）
            commonHeaders: {
                'Content-Type': 'application/json',
                'ngrok-skip-browser-warning': '1'
            }
        };
    },
    mounted() {
        // 页面加载时测试连接
        this.testConnection();
    },
    methods: {
        // 返回主页面消息界面
        backToHomeMessage() {
            this.$router.push('/home/message');
        },
        
        // 发送消息（非流式请求，单次交互）
        async sendMessage() {
            const prompt = this.message.trim();
            if (!prompt) return;
            
            // 添加用户消息到界面
            this.addMessage('user', prompt);
            this.message = '';
            this.scrollToBottom();
            
            // 显示"正在输入"状态
            const typingId = this.addTypingIndicator();
            
            try {
                // 发送HTTPS请求（带ngrok请求头）
                const response = await fetch(this.API_URL, {
                    method: 'POST',
                    headers: this.commonHeaders,
                    body: JSON.stringify({ prompt: prompt })
                });
                
                // 移除"正在输入"状态
                this.removeTypingIndicator(typingId);
                
                if (!response.ok) {
                    const error = await response.json().catch(() => ({}));
                    throw new Error(error.result || `请求失败: ${response.status}`);
                }
                
                const data = await response.json();
                // 根据后端返回的状态处理结果
                if (data.status === 'success') {
                    this.addMessage('ai', data.result);
                } else {
                    this.addMessage('ai', `错误: ${data.result}`, true);
                }
                this.scrollToBottom();
                
            } catch (error) {
                // 移除"正在输入"状态
                this.removeTypingIndicator(typingId);
                
                // 显示错误消息
                this.addMessage('ai', `请求失败: ${error.message}`, true);
                this.scrollToBottom();
                console.error('发送消息失败:', error);
            }
        },
        
        // 添加消息到界面
        addMessage(sender, content, isError = false) {
            this.messages.push({
                sender: sender,
                content: content,
                isError: isError
            });
        },
        
        // 添加"正在输入"指示器
        addTypingIndicator() {
            const typingId = `typing-${Date.now()}`;
            this.typingId = typingId;
            this.isTyping = true;
            return typingId;
        },
        
        // 移除"正在输入"状态
        removeTypingIndicator(id) {
            if (this.typingId === id) {
                this.isTyping = false;
                this.typingId = "";
            }
        },
        
        // 滚动到最新消息
        scrollToBottom() {
            this.$nextTick(() => {
                const container = this.$refs.messagesContainer;
                if (container) {
                    container.scrollTop = container.scrollHeight;
                }
            });
        },
        
        // 测试连接状态
        async testConnection() {
            try {
                await fetch(this.API_URL, {
                    method: 'OPTIONS',
                    headers: this.commonHeaders
                });
                
                this.statusText = `已连接到: ${this.API_URL}`;
                this.statusClass = 'text-xs text-green-600 mt-2 text-center';
            } catch (error) {
                this.statusText = `连接失败: ${error.message}`;
                this.statusClass = 'text-xs text-red-600 mt-2 text-center';
            }
        }
    }
}
</script>

<style>
