#dialog {
    --van-nav-bar-arrow-size: 20px;
    --van-nav-bar-icon-color: black;
    --van-nav-bar-title-text-color: black;
    --van-nav-bar-background-color: #EFF1FD;
    height: 100vh;
    display: flex;
    flex-direction: column;
}
#dialog .van-nav-bar__title {
    line-height: 16px;
}

