#dialog_content {
    background-color: #EDEDF5;
    flex: 1;
    padding: 70px 15px 20px; /* 预留导航栏高度 */
    overflow-y: auto;
}

.messages {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

/* 消息项样式 */
.message-item {
    display: flex;
    align-items: flex-start;
}

