}

/* 底部栏样式 */
#dialog_bottombar {
    height: auto;
}
#dialog_bottombar_inside {
    position: fixed;
    bottom: 0px;
    width: 95%;
    padding: 8px 2.5% 5px;
    background-color: #EFF1FD;
}
#dialog_bottombar_inside .van-row {
    padding-top: 8px;
}

/* 状态提示样式 */
.status-bar {
    position: fixed;
    bottom: 100px;
    left: 0;
    width: 100%;
    text-align: center;
}
</style>